// Cover
@mixin cover($zindex) {
	position: absolute;
	z-index: $zindex;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: block;
}
// Clearfix
@mixin clearfix {
	&:after {
		content: '';
		display: table;
		clear: both;
	}
}
// Media Queries	
$phone-width: 599px; /* 599px */
$tablet-width: 899px; /* 899px */
$md-width: 1200px; /* 1200px */
$laptop-width: 1599px; /* 1599px */
@mixin phone {
	@media handheld, only screen and (max-width: #{$phone-width}) {
		@content;
	}
}
@mixin tablet {
	@media handheld, only screen and (max-width: #{$tablet-width}) {
		@content;
	}
}
@mixin md {
	@media handheld, only screen and (max-width: #{$md-width}) {
		@content;
	}
}
@mixin laptop {
	@media handheld, only screen and (max-width: #{$laptop-width}) {
		@content;
	}
}
// Custom Media Queries
// Max Width
	@mixin max-width($width) {
		@media handheld,
		only screen and (max-width: #{$width}) {
			@content;
		}
	}
// Min Width
	@mixin min-width($width) {
		@media handheld,
		only screen and (min-width: #{$width}) {
			@content;
		}
	}
// Wrap Links
@mixin wrap-link {
	overflow-wrap: break-word;
	word-wrap: break-word;
	-ms-word-break: break-all;
	word-break: break-all;
	word-break: break-word;
	-ms-hyphens: auto;
	-moz-hyphens: auto;
	-webkit-hyphens: auto;
	hyphens: auto;
}
// Placeholder
@mixin placeholder {
	 ::-webkit-input-placeholder {
		@content
	}
	 :-moz-placeholder {
		@content
	}
	 ::-moz-placeholder {
		@content
	}
	 :-ms-input-placeholder {
		@content
	}
	 ::-ms-input-placeholder {
		@content
	}
	 :placeholder {
		@content
	}
	.placeholdersjs {
		@content
	}
}
// Grid Spacing
@mixin grid-spacing($spacing-left, $spacing-bottom: 0) {
	.grid {
		margin-left: -$spacing-left;
	}
	.grid__item {
		padding-left: $spacing-left;
		padding-bottom: $spacing-bottom;
	}
}
// Aspect Ratio
@mixin aspect-ratio($percent) {
	position: relative;
	overflow: hidden;
	height: 0;
	padding-bottom: $percent;
}
// Visually Hidden But Accessible
@mixin visually-hidden {
	border: 0;
	clip: rect(1px, 1px, 1px, 1px);
	-webkit-clip-path: inset(50%);
	clip-path: inset(50%);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
	word-wrap: normal !important;
}
// Zoom Animation
@mixin zoom {
	.will-zoom {
		transition: $transition-slow;
	}
	&:hover .will-zoom {
		transform: scale(1.1,1.1);
	}
}